  debouncedSetHpRange: (param: number[]) => void,
  attackRange: number[],
  debouncedSetAttackRange: (param: number[]) => void,
  defenseRange: number[],
  debouncedSetDefenseRange: (param: number[]) => void,
  specialAttackRange: number[],
  debouncedSetSpecialAttackRange: (param: number[]) => void,
  specialDefenseRange: number[],
  debouncedSetSpecialDefenseRange: (param: number[]) => void,
  speedRange: number[],
  debouncedSetSpeedRange: (param: number[]) => void,
