export const isUltraBeastByNum = (num: number): boolean => contains(ULTRA_BEASTS, num);

export const isPastParadoxPokemonByNum = (num: number): boolean => contains(PAST_PARADOX, num);

export const isFutureParadoxPokemonByNum = (num: number): boolean => contains(FUTURE_PARADOX, num);

export const isParadoxPokemonByNum = (num: number): boolean => isParadoxPokemonByNum(num)
|| isFutureParadoxPokemonByNum(num);

export const filterByGenerations = (
  Pokedex: { [speciesid: string]: SpeciesData },
  gensAllowed: { [key: string]: boolean },
) => {
  const gensAllowedArray = Object.keys(gensAllowed)
    .map((gen: string): number => {
      if (gensAllowed[gen]) return parseInt(gen, 10);
      return 0;
    })
    .filter((gen: number) => gen !== 0);
  // Object.keys(Pokedex)
  // .filter(filterByGeneration(Pokedex: {[speciesid: string]: SpeciesData}, gensAllowedArray))
  return (speciesId: string): boolean => contains(
    gensAllowedArray,
    getGenIntroducedByStats(Pokedex[speciesId]),
  );
};

export const filterByTypes = (
  Pokedex: { [speciesid: string]: SpeciesData },
  typesAllowed: { [key: string]: boolean },
) => {
  const typesAllowedArray = Object.keys(typesAllowed)
    .map((type: string): string | null => {
      if (typesAllowed[type]) return type;
      return null;
    })
    .filter((type: string | null): boolean => type != null);
  if (typesAllowedArray.length === 0) return () => true;
  return (speciesId: string): boolean => Pokedex[speciesId].types.some(
    (type: string) => contains(typesAllowedArray, type),
  );
};

export const filterByTypesInclusive = (
  Pokedex: { [speciesid: string]: SpeciesData },
  typesAllowed: string[],
) => {
  if (typesAllowed.length === 0) return () => true;
  const typesSelected = new Set(typesAllowed);
  return (speciesId: string): boolean => {
    let counter = 0;
    const monTypes = Pokedex[speciesId].types;
    monTypes.forEach((type: string) => {
      if (typesSelected.has(type)) counter += 1;
    });
    return counter === typesAllowed.length;
  };
};

export const filterBaseSpecies = (
  Pokedex: { [speciesid: string]: SpeciesData },
  baseSpeciesAllowed: boolean = false,
) => {
  if (baseSpeciesAllowed) return () => true;
  return (speciesId: string): boolean => 'baseSpecies' in Pokedex[speciesId];
};

export const countBaseSpecies = (
  Pokedex: { [speciesid: string]: SpeciesData },
  speciesIds: string[],
): number => {
  const unique = new Set<number>();
  speciesIds.forEach((speciesId) => {
    const { num } = Pokedex[speciesId];
    unique.add(num);
  });
  return unique.size;
};

export const filterUltraBeasts = (
  Pokedex: { [speciesid: string]: SpeciesData },
  ultraBeastsAllowed = true,
) => {
  if (ultraBeastsAllowed) return () => true;
  return (speciesId: string): boolean => !isUltraBeastByNum(Pokedex[speciesId].num);
};

export const filterParadoxPokemon = (
  Pokedex: { [speciesid: string]: SpeciesData },
  paradoxPokemonAllowed = true,
) => {
  if (paradoxPokemonAllowed) return () => true;
  return (speciesId: string): boolean => !isParadoxPokemonByNum(Pokedex[speciesId].num);
};

export const filterMegas = (
  Pokedex: { [speciesid: string]: SpeciesData },
  megasAllowed = true,
) => {
  if (megasAllowed) return () => true;
  return (speciesId: string): boolean => {
    const name = Pokedex[speciesId].name.toLowerCase();
    return !name.includes('-mega') && !name.includes('-primal') && !name.includes('-ultra');
  };
};

export const filterGMaxes = (
  Pokedex: { [speciesid: string]: SpeciesData },
  gmaxAllowed = true,
) => {
  if (gmaxAllowed) return () => true;
  return (speciesId: string): boolean => !Pokedex[speciesId].name.toLowerCase().includes('-gmax');
};

export const filterLegendaries = (
  Pokedex: { [speciesid: string]: SpeciesData },
  legendariesAllowed = true,
) => {
  if (legendariesAllowed) return () => true;
  return (speciesId: string): boolean => {
    const { num } = Pokedex[speciesId];
    return !contains(LEGENDARIES, num) && !contains(SUB_LEGENDARIES, num);
  };
};

export const filterMythicals = (
  Pokedex: { [speciesid: string]: SpeciesData },
  mythicalsAllowed = true,
) => {
  if (mythicalsAllowed) return () => true;
  return (speciesId: string): boolean => !contains(MYTHICALS, Pokedex[speciesId].num);
};

export const filterByStats = (
  Pokedex: { [speciesid: string]: SpeciesData },
  stat: string,
  statRange: number[],
) => {
  if (statRange[0] === 1 && statRange[1] === 255) return () => true;
  return (speciesId: string): boolean => {
    const { baseStats } = Pokedex[speciesId];
    return baseStats[stat] >= statRange[0] && baseStats[stat] <= statRange[1];
  };
};

export const filterRegionals = (
  Pokedex: { [speciesid: string]: SpeciesData },
  regionalsAllowed = true,
) => {
  if (regionalsAllowed) return () => true;
  return (speciesId: string): boolean => !REGIONAL_SUFFIX.some(
    (suffix: string): boolean => containsSuffix(suffix, Pokedex[speciesId].name.toLowerCase()),
  );
};

